import React, { useEffect, useState } from 'react'
import { Navbar } from '../UI/Navbar';
import { messages } from '../../helpers/calendar-messages';
import {Calendar, momentLocalizer} from 'react-big-calendar'
import moment from 'moment';
import 'react-big-calendar/lib/css/react-big-calendar.css'
import 'moment/locale/es';
import { CalendarEvent } from './CalendarEvent';
import { Calendarmodal } from './Calendarmodal';
import { useDispatch, useSelector } from 'react-redux';
import { uiOpenModal } from '../../actions/ui';
import { eventStartLoading, setActiveEvent } from '../../actions/calendar';
import { AddNewFab } from '../UI/addNewFab';
import { DeleteEventFab } from '../UI/DeleteEventFab';
moment.locale('es')
const localizer= momentLocalizer(moment);


export const CalendarScreen = () => {
    const uid = useSelector( state=> state.auth.uid)
    const dispatch = useDispatch();
    const activeEvent = useSelector(state => state.calendar.activeEvent)
    const events = useSelector(state => state.calendar.events) 
    const [lastView, setlastView] = useState(localStorage.getItem('lastView') || 'month');
    useEffect(() =>{
        dispatch(eventStartLoading());
    },[dispatch])

    const onDoubleClick = (e) => {
        
        dispatch(uiOpenModal())
    }

    const onSelectEvent = (e) => {
        
        dispatch(setActiveEvent(e))
    }

    const onViewChange = (e) => {
        setlastView(localStorage.setItem('lastView', e)|| e)
    }

    const eventStyleGetter = (e , start, end, isSelected) => {
        
        const style = {
            backgroundColor:(e.user._id == uid) ? '#367cf7' : '#465660',
            borderRadius: '0px',
            opcacity: 0.8,
            display: 'block',
            color: 'white'
        }
        return {
            style
        }
    }

    return (
    <div className='calendar-screen'>
        <Navbar />
        <Calendar
            localizer={localizer}
            events={events}
            startAccessor="start"
            endAccessor="end"
            messages={messages}
            eventPropGetter={eventStyleGetter}
            onSelectEvent={onSelectEvent}
            onView={ onViewChange }
            onDoubleClickEvent={onDoubleClick}
            components={{ 
                event: CalendarEvent ,
                
            }}
            view={lastView}
        />
        <AddNewFab/>
        {activeEvent && <DeleteEventFab />}
        <Calendarmodal />
    </div>
  )
}
