import React, { useEffect, useState } from 'react'
import Modal from 'react-modal';
import DateTimePicker from 'react-datetime-picker';
import moment from 'moment';


import { customStyles } from '../../helpers/modal-styles';

import '../../styles/modal.css'
import 'react-datetime-picker/dist/DateTimePicker.css';
import 'react-calendar/dist/Calendar.css';
import 'react-clock/dist/Clock.css';
import Swal from 'sweetalert2';
import { useDispatch, useSelector } from 'react-redux';
import { uiCloseModal } from '../../actions/ui';
import { StartAddNewEvent, clearActivenote, startEventUpdate } from '../../actions/calendar';

Modal.setAppElement('#root');
const now = moment().locale('es').minutes(0).seconds(0).add(1, 'hours');
const end = now.clone().add(1, 'hours');

const initEvent = {
    title: '',
    notes: '',
    start: now.toDate(),
    end: end.toDate(),
}
export const Calendarmodal = () => {
    const isOpen = useSelector( state => state.ui.modalOpen );
    const activeEvent = useSelector(state => state.calendar.activeEvent)
    const [titleValid, settitleValid] = useState(true)
    const dispatch = useDispatch();
    const [formValues, setformValues] = useState(initEvent)
    const [initDate, setDate] = useState(now.toDate())
    const [endDate, setendDate] = useState(end);

    const {title, notes} = formValues;
    useEffect(() => {
        if (activeEvent) {
            setformValues(activeEvent)
        }
    }, [activeEvent, setformValues])
    

    const closeModal = () => {
        dispatch(uiCloseModal())
        dispatch(clearActivenote())
        setformValues(initEvent)
    }
    const handleInitDate = (e) => {
        setDate(e)
        setformValues({
            ...formValues,
            start: e
        })
    };
    const handleFinishDate = e => {
        setendDate(e)
        setformValues({
            ...formValues,
            end: e
        })
    }

    const handleInputChange = ({target})=>{
        setformValues({
            ...formValues,
            [target.name]: target.value
        })
    }
    const handleSubmit = e =>{
        e.preventDefault();
        const momentStart = moment(formValues.start);
        const momentEnd = moment(formValues.end)
        if(momentStart.isSameOrAfter(momentEnd)){
            
            Swal.fire('Error', 'End date must be before start date')
            return;
        }
        if(title.trim().length < 2){
            return settitleValid(false);
        }
        settitleValid(true);
        closeModal();
        if (!activeEvent){
            dispatch(StartAddNewEvent(formValues))
        }else{
            dispatch(startEventUpdate(formValues))
        }
    }
return (
    <Modal
        isOpen={isOpen}
        /* onAfterOpen={afterOpenModal} */
        onRequestClose={closeModal} 
        style={customStyles}
        closeTimeoutMS={200}
        contentLabel="Example Modal"
        className='modal'
        overlayClassName='modal-fondo'
    >
        <h1> Nuevo evento </h1>
        <hr />
        <form className="container" onSubmit={handleSubmit}>

            <div className="form-group mb-2">
                <label>Fecha y hora inicio</label>
                <DateTimePicker onChange={handleInitDate} value={ initDate } className='form-control react-datetime-picker' />
            </div>

            <div className="form-group mb-2">
                <label>Fecha y hora fin</label>
                <DateTimePicker onChange={handleFinishDate} minDate={ initDate} value={ endDate } className='form-control react-datetime-picker' />

            </div>

            <hr />
            <div className="form-group mb-2">
                <label>Titulo y notas</label>
                <input 
                    type="text" 
                    className={`form-control ${!titleValid && 'is-invalid'}`}
                    placeholder="Ttulo del evento"
                    name="title"
                    autoComplete="off"
                    value={title}
                    onChange={handleInputChange}
                />
                <small id="emailHelp" className="form-text text-muted">Una descripcin corta</small>
            </div>

            <div className="form-group mb-2">
                <textarea 
                    type="text" 
                    className="form-control"
                    placeholder="Notas"
                    rows="5"
                    name="notes"
                    value={notes}
                    onChange={handleInputChange}
                ></textarea>
                <small id="emailHelp" className="form-text text-muted">Informacin adicional</small>
            </div>

            <button
                type="submit"
                className="btn btn-outline-primary btn-block"
            >
                <i className="far fa-save"></i>
                <span> Guardar</span>
            </button>

        </form>
    </Modal>
  )
}
