import Swal from "sweetalert2";
import { fetchConToken, fetchSinToken } from "../helpers/fetch"
import { types } from "../types/types";
import { LogoutClean } from "./calendar";


export const startLogin = (email, password) => {
    return async dispatch=>{
        /* console.log(email, password) */
        const resp= await fetchSinToken('auth',{ email , password }, 'POST');
        const body = await resp.json();
        if(body.ok){
            localStorage.setItem('token',body.token);
            localStorage.setItem('token-init-date',new Date().getTime());
            dispatch(login({
                uid: body.uid,
                name: body.name
            }))
        }else{
            Swal.fire({
                title: 'Invalid email or password',
                icon: 'error'
            })
        }
    }
}

export const startRegisterUser = (name, email, password) => {
    return async dispatch=>{
        const res = await fetchSinToken('auth/new', {name, email, password}, 'POST')
        const body= await res.json();
        if(body.ok){
            localStorage.setItem('token',body.token);
            localStorage.setItem('token-init-date',new Date().getTime());
            dispatch(login({
                uid: body.uid,
                name: body.name
            }))
        }else{
            Swal.fire({
                title: 'There is an user with this email already registered',
                icon: 'error'
            })
        }
    }
};

export const  startChecking = () =>{
    return async dispatch =>{
        const res= await fetchConToken('auth/renew')
        const body = await res.json();
        
        if(body.ok){
            localStorage.setItem('token',body.token);
            localStorage.setItem('token-init-date',new Date().getTime());
            dispatch(login({
                uid: body.user.uid,
                name: body.user.name
            }))
        }else{
            Swal.fire({
                title: body.msg,
                icon: 'error'
            })
            dispatch(finishChecking())
        }
    }
}

const login = (user) => ({
    type: types.authLogin,
    payload: user
});
export const startLogout = () =>{
    return dispatch =>{
        localStorage.clear();
        dispatch(LogoutClean());
        dispatch(logout());
    }
}

const logout = () =>({type: types.authLogout})

const finishChecking = ()=> ({type: types.authCheckingFinished})