import Swal from "sweetalert2";
import { dateChanger } from "../helpers/date-changer";
import { fetchConToken } from "../helpers/fetch";
import { types } from "../types/types";

export const StartAddNewEvent = (event) => {
    return async (dispatch, getState) =>{
        const {uid, name} = getState().auth;
        try {
            const res = await fetchConToken('events', event, 'POST')
            const body = await res.json();
            if(body.ok){
                event.id= body.evento.id;
                event.user={
                    _id: uid,
                    name 
                }
                dispatch(eventAddNew(event))
            }
        } catch (error) {
            console.error(error);
        }
        
    }
}

const eventAddNew = (event) => ({
    type: types.eventsAddNew,
    payload: event
});

export const LogoutClean = () =>({type: types.eventsLogout})

export const setActiveEvent = (event) => ({
    type: types.eventsSetActive,
    payload: event
});

export const clearActivenote = () => ({
    type: types.eventClearActive
});

export const startEventUpdate = event =>{
    return async dispathc => {
        
        try {
            const res= await fetchConToken(`events/${event.id}`, event, 'PUT');
            const body = await res.json();
            
            if(body.ok){
                dispathc(eventUpdate(event))
            }else{
                Swal.fire({
                    title: body.msg,
                    icon: 'error'
                })
            }
        } catch (error) {
            console.error(error)
        }
    }
}

const eventUpdate = (event) => ({
    type: types.eventUpdate,
    payload: event
});

export const startDeleteEvent = () =>{
    return async (dispatch,getState) =>{
        const event = getState().calendar.activeEvent
        try {
            const res= await fetchConToken(`events/${event.id}`, {}, 'DELETE');
            const body = await res.json();
            
            if(body.ok){
                dispatch(eventDelete())
            }else{
                Swal.fire({
                    title: body.msg,
                    icon: 'error'
                })
            }
        } catch (error) {
            console.error(error)
        }
    }
}

const eventDelete = () => ({
    type: types.eventDelete
})

export const eventStartLoading = () => {
    return async dispatch =>{
        try {
            const res = await fetchConToken('events')
            const body = await res.json();
            const events = dateChanger(body.events);
            dispatch(eventsLoaded(events));
        } catch (error) {
            console.error(error);
        }
    }
}

const eventsLoaded = (events)=>({
    type: types.eventLoaded,
    payload: events
})