import React from 'react';
import '../../styles/login.css';
import { useForm } from '../../hooks/useForm';
import { useDispatch } from 'react-redux';
import { startLogin, startRegisterUser } from '../../actions/auth';
import Swal from 'sweetalert2';

export const Login = () => {
    const dispatch = useDispatch();
    const [FormLoginValues, handleLoginInputChange] = useForm({
        lEmail: 'iantespinillo@gmail.com',
        lPassword: '123456',
    })
    const [FormRegisterValues, handleRegisterInputChange] = useForm({
        rName:'Pepe',
        rEmail: 'pepe@gmail.com',
        rPassword: '12345678',
        rPassword2: '12345678',
    })
    const {lEmail, lPassword} = FormLoginValues
    const {rName, rEmail, rPassword, rPassword2} = FormRegisterValues
    const handleLogin = e =>{
        e.preventDefault();
        dispatch(startLogin(lEmail, lPassword))
    }
    const handleRegister = e =>{
        e.preventDefault();
        if( rPassword != rPassword2){
            return Swal.fire({
                title: 'The passwords are not the same',
                icon: 'error'
            })
        }
        dispatch(startRegisterUser(rName, rEmail, rPassword))
    }
    return (
        <div className="container login-container">
            <div className="row">
                <div className="col-md-6 login-form-1">
                    <h3>Ingreso</h3>
                    <form onSubmit={handleLogin}>
                        <div className="form-group mb-2">
                            <input 
                                type="text"
                                className="form-control"
                                placeholder="Correo"
                                name='lEmail'
                                value={lEmail}
                                onChange={handleLoginInputChange}
                            />
                        </div>
                        <div className="form-group mb-2">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contrasea"
                                name='lPassword'
                                value={lPassword}
                                onChange={handleLoginInputChange}
                            />
                        </div>
                        <div className="form-group mb-2">
                            <input 
                                type="submit"
                                className="btnSubmit"
                                value="Login" 
                            />
                        </div>
                    </form>
                </div>

                <div className="col-md-6 login-form-2">
                    <h3>Registro</h3>
                    <form onSubmit={handleRegister}>
                        <div className="form-group mb-2">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Nombre"
                                name='rName'
                                value={rName}
                                onChange={handleRegisterInputChange}
                            />
                        </div>
                        <div className="form-group mb-2">
                            <input
                                type="email"
                                className="form-control"
                                placeholder="Correo"
                                name='rEmail'
                                value={rEmail}
                                onChange={handleRegisterInputChange}
                            />
                        </div>
                        <div className="form-group mb-2">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contrasea" 
                                name='rPassword'
                                value={rPassword}
                                onChange={handleRegisterInputChange}
                            />
                        </div>

                        <div className="form-group mb-2">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Repita la contrasea" 
                                name='rPassword2'
                                value={rPassword2}
                                onChange={handleRegisterInputChange}
                            />
                        </div>

                        <div className="form-group mb-2">
                            <input 
                                type="submit" 
                                className="btnSubmit" 
                                value="Crear cuenta" />
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}