import React from 'react'
import { Navigate } from 'react-router-dom';
import PropTypes from 'prop-types'

export const PublicRoutes = ({isLogged, children}) => {
    if(!isLogged){
        return children;
    }else{
        return <Navigate to='/' />;
    }
}

PublicRoutes.propTypes= {
    isLogged: PropTypes.bool.isRequired
}